<style lang="css" scoped>

/* The actual timeline (the vertical ruler) */
.timeline {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: #969595;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}

/* Container around content */
.tl-container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
}

/* The circles on the timeline */
.tl-container::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -13px;
    background-color: #969595;
    border: 4px solid #EAC657;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}

/* Place the container to the left */
.left { left: 0; }

/* Place the container to the right */
.right { left: 50%; }

/* Add arrows to the left container (pointing right) */
.left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid #969595;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #969595;
}

/* Add arrows to the right container (pointing left) */
.right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid #969595;
    border-width: 10px 10px 10px 0;
    border-color: transparent #969595 transparent transparent;
}

/* Fix the circle for containers on the right side */
.right::after { left: -12px; }

/* The actual content */
.tl-content {
    padding: 20px 30px;
    color: white;
    background-color: #969595;
    position: relative;
    border-radius: 6px;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  /* Place the timelime to the left */
  .timeline::after { left: 27px; }

  /* Full-width containers */
  .tl-container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  /* Make sure that all arrows are pointing leftwards */
  .tl-container::before {
    left: 60px;
    border: medium solid #969595;
    border-width: 10px 10px 10px 0;
    border-color: transparent #969595 transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left::after, .right::after { left: 15px; }

  /* Make all right containers behave like the left ones */
  .right { left: 0%; }
}

</style>



